import React from 'react'
import beornings from '../images/race_beorning.jpg'

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img className="race-profile" src={beornings} />
            </div>
            <div className="col-lg-8">
                <h1>Beornings</h1>
                <p>The origins of <strong>Beornings</strong> are not known, save that they dwelt in the Vales of Anduin between Mirkwood
                and the Great River in the late Third Age. Unlike other Men, they had the ability to change into bears. They enjoyed being a 
                bear so much they forgot how to turn back into humans. They seem to have the same life-span of mortal men, and could speak in
                both the tongue of men and of animals. They are not very fond of Dwarves, and were bitter enemies of the Goblins. They hunt
                neither bird nor beast, their diet consisting mostly of cream and honey.</p>
                <h1>Attributes</h1>
                <p>Beornings receive a bonus to bashing and are harder to detect while hiding. This race is intended for seasoned players because of
                    their item, mount, and spell restrictions. They are incapable of casting any Mage spells.
                </p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Very High</td>
                            <td>Very Low</td>
                            <td>Low</td>
                            <td>Normal</td>
                            <td>Very High</td>
                            <td>Low</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


